<template>
  <div class="food-page">
    <img class="BgImage" v-if="food_data.image != undefined" :src="food_data.image.split('<###^^&&###>')[0]" alt="" />
    <div class="btn-back" @click="back">
      <img src="../assets/back.png" alt="" class="back" />
    </div>
    <div class="text-area">
      <food-description :food="food_data" />
      <food-ingredient  :methods="method"/>
      <food-process :methods="method"/>
    </div>
    <bottom-bar />
  </div>
</template>

<script>
import foodDescription from "../components/foodDescription.vue";
import FoodIngredient from "../components/foodIngredient.vue";
import FoodProcess from "../components/foodProcess.vue";
import axios from "axios";
import BottomBar from '../components/bottomBar.vue';

export default {
  components: { foodDescription, FoodIngredient, 
  FoodProcess, 
  BottomBar },
  data() {
    return {
      food_id: this.$router.history.current.params.pathMatch,
      food_data: {},
      method: {},
    };
  },
  props: {
    food: {
      type: Object,
    },
    methods: {
      type: Object,
    },
  },
  methods: {
    back() {
      window.history.back();
    },
    getFood() {
      axios
        .request({
          url: "https://homedelicious.ml/api/foods",
          method: "get",
          params: {
            food_id: this.food_id,
          },
        })
        .then((response) => {
          console.log(response.data);
          this.food_data = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getMethods(){
      axios
        .request({
          url: "https://homedelicious.ml/api/methods",
          method: "get",
          params: {
            food_id: this.food_id,
          },
        })
        .then((response) => {
          console.log(response.data);
          this.$store.commit("updateMethod", response.data)
        })
        .catch((error) => {
          console.log(error);
        });
    }
  },
  mounted() {
    this.getMethods();
    this.getFood();
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/style/variable.scss";

.food-page {
  width: 100vw;
  min-height: 100vh;
  background-color: $bgc;
  position: relative;
  background-color: $bgc;
  .BgImage {
    z-index: 1;
    width: 100vw;
    height: 110vw;
    object-fit: cover;
    position: fixed;
    top: 0;
  }
  .btn-back {
    z-index: 50;
    background-color: $bgc;
    position: fixed;
    top: 5vw;
    left: 5vw;
    width: 15vw;
    border-radius: 5px;
    img {
      width: 80%;
      margin-left: 10%;
    }
    filter: $shadow;
  }
  .bottom-bar {
    z-index: 50;
    width: 100%;
    height: 8vh;
    background-color: #fff;
    position: fixed;
    bottom: 0;
  }
  .text-area{
    box-sizing: border-box;
    padding: 8vw 8vw 20vw 8vw;
    z-index: 10;
    min-height: 50vh;
    width: 100vw;
    background-color: #fff;
    position: relative;
    top: 50vh;
    border-radius: 30px 30px 0 0;
    filter: $shadow;
  }
}
</style>